import React, { useState, useEffect } from 'react';
import { Eye, Search, Menu, X, Star } from 'lucide-react';

// --- MOCK API & TYPES ---

// Helper to fetch data (using Jikan API for demo purposes)
async function getPopularAnime() {
  try {
    const response = await fetch('https://api.jikan.moe/v4/top/anime?filter=bypopularity&limit=20');
    if (!response.ok) throw new Error('Network response was not ok');
    const data = await response.json();
    return { data: data.data };
  } catch (error) {
    console.error("API Fetch failed, using fallback data", error);
    // Fallback mock data if API fails/rate limits
    return {
      data: [
        { mal_id: 1, title: "Fullmetal Alchemist: Brotherhood", images: { jpg: { image_url: "https://cdn.myanimelist.net/images/anime/1223/96541.jpg" } }, score: 9.10, synopsis: "After a horrific alchemy experiment goes wrong..." },
        { mal_id: 2, title: "Bleach: Sennen Kessen-hen", images: { jpg: { image_url: "https://cdn.myanimelist.net/images/anime/1764/126627.jpg" } }, score: 9.03, synopsis: "The substitute Soul Reaper Ichigo Kurosaki..." },
        { mal_id: 3, title: "Steins;Gate", images: { jpg: { image_url: "https://cdn.myanimelist.net/images/anime/5/73199.jpg" } }, score: 9.07, synopsis: "Self-proclaimed mad scientist Rintarou Okabe..." },
        { mal_id: 4, title: "Gintama°", images: { jpg: { image_url: "https://cdn.myanimelist.net/images/anime/3/72078.jpg" } }, score: 9.06, synopsis: "Gintoki, Shinpachi, and Kagura return..." },
        { mal_id: 5, title: "Kaguya-sama: Love is War", images: { jpg: { image_url: "https://cdn.myanimelist.net/images/anime/1105/124280.jpg" } }, score: 9.05, synopsis: "At the renowned Shuchiin Academy..." },
      ]
    };
  }
}

